import React from 'react'

import Box from '../../components/Box'
import Text from '../../components/Text'
import BeforeStart from '../../components/BeforeStart'
import { responsive } from '../../components/ThemeProvider/theme'

const OptOne = () => {
  return (
    <BeforeStart
      background="linear-gradient(#CABFEC 0%, transparent 100%)"
      noCircle
    >
      <Box flex={1}>
        <Text fontSize={responsive('1em', '2em')}></Text>
        <Text.Bold mt="2rem" fontSize={responsive('1.25em', '2.25em')}><br /></Text.Bold>
      </Box>
    </BeforeStart>
  )
}

export default OptOne
