import React from 'react'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Container from '../../../components/Container'
import Flex from '../../../components/Flex'
import Image from '../../../components/Image'
import Button from '../../../components/Button'

import chart_1 from './4-1-1.svg'
import chart_2 from './4-1-2.svg'
import CircleBgModule from '../../../components/CircleBgModule'
import { responsive } from '../../../components/ThemeProvider/theme'

const texts = [
  {
    title: '',
    text: '',
    src: chart_1
  },
  {
    title: '',
    text: '',
    src: chart_2
  },
]

const ChangeTwo = () => {
  return (
    <CircleBgModule bg="white">
      <Box position="relative" height="45vh">
        <Box.AbsCenter color="white">
          <Text.Bold fontSize={responsive('1.125em', '2.25em')}></Text.Bold>
          <Text fontSize={responsive('0.875em', '1.5em')}></Text>
        </Box.AbsCenter>
      </Box>
      <Box bg="linear-gradient(#82CAD0 0%, #FFFFFF 66%)">
        <Container height="55vh">
          <Flex flexDirection={responsive('column', 'row')} my="1em" justifyContent="center">
            {texts.map(({ title, text, src }, i) => (
              <Box width={responsive('100%', '42.5%')} px="1em" key={i}>
                <Flex flexDirection={responsive('row', 'column')} alignItems={responsive('center', 'flex-start')}>
                  <Box.Inline
                    fontSize={responsive('0.875em', '1.25em')}
                    borderRadius="1em"
                    color="white"
                    px="1em"
                    py="0.25em"
                    bg="linear-gradient(90deg, #82CAD0 0%, #053D72 60%)"
                  >{title}</Box.Inline>
                  <Text
                    mt={responsive('0', '0.5em')}
                    fontSize={responsive('0.75em', '1.125em')}
                    color="prussianBlue"
                  >{text}</Text>
                </Flex>
                <Box>
                  <Image src={src} />
                </Box>
              </Box>
            ))}
          </Flex>
          <Box pt="0.5em" borderTop="1px solid">
            : 
          </Box>
        </Container>
      </Box>
    </CircleBgModule>
  )
}

export default ChangeTwo
