import React from 'react'

import Box from '../../components/Box'
import Text from '../../components/Text'
import BeforeStart from '../../components/BeforeStart'
import { responsive } from '../../components/ThemeProvider/theme'

const ChangeOne = () => {
  return (
    <BeforeStart
      background="linear-gradient(#82CAD0 0%, transparent 100%)"
      noCircle
    >
      <Box flex={1}>
        <Text.Bold fontSize={responsive('1.5em', '2.25em')}></Text.Bold>
        <Text mt="2rem" fontSize={responsive('1em', '1.5em')}>
           
        </Text>
      </Box>
    </BeforeStart>
  )
}

export default ChangeOne
