import React from 'react'

import Box from '../../../components/Box'
import Container from '../../../components/Container'
import Flex from '../../../components/Flex'
import Image from '../../../components/Image'
import Button from '../../../components/Button'
import Title from '../../../components/Title'

import number from './chart-3-1-sub.svg'
import Chart31 from './Chart31'

const OptTwo = () => {
  return (
    <Box bg="white">
      <Title
        subTitle=""
        text={" - \n"}
        note=""
        explain
        exTitle="?"
        exText=""
      />
      <Container py="3em">
        <Box>
          <Chart31 />
          <Image src={number} />
        </Box>
        <Flex mt="1em" alignItems="center">
          <Box mr="1em">
            <Button.SingleSharp
              target="_blank"
              href="http://www.newfinancecapital.com/zh-tw/tw/asset-management/insights/global-credit-income-2020/"
              bg="linear-gradient(90deg, #002962 25%, #CABFEC 100%)"
              _hover={{ color: 'white', textDecoration: 'none' }}
            >
              
            </Button.SingleSharp>
          </Box>
          <Box pt="0.25em" borderTop="1px solid">
            : , Bloomberg, JP Morgan 2020/10/30 *180 Bloomberg 
          </Box>
        </Flex>
      </Container>
    </Box>
  )
}

export default OptTwo
